* {
  margin: 0;
  padding: 0;
}
html, body, #root {
  width: 100%;
  height: 100%;
  max-width: 100vw;
  overflow-x: hidden;
}

::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

::-webkit-scrollbar
{
	width: 6px;
	background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb
{
	background-color: #000000;
}